import { useRoute, useRouter } from "vue-router";
import { CurrentRServerKey, GlobalRServerKey, EmptyPin } from "./initParams";
import Server, { createRxFunc } from "./RouterPin";
import { inject } from "vue";
/**
 *
 * @returns 当前路由派发
 */
function useRoutePin() {
    var currentServe = inject(CurrentRServerKey);
    if (!currentServe) {
        return EmptyPin;
    }
    return createRxFunc(currentServe.value, useRoute());
}
/**
 *
 * @returns 全局派发
 */
function useRouterPin() {
    var currentServe = inject(GlobalRServerKey);
    if (!currentServe) {
        return EmptyPin;
    }
    return createRxFunc(currentServe, useRouter());
}
function dispatchEvent(type, data, isAsync, target) {
    return Server.dispatch(type, data, isAsync, target);
}
export { Server as VueRouterPin, useRoutePin, useRouterPin, dispatchEvent };
